.home{
  height: 100%;
  .center{
    position: absolute;
    top: 50%;
    left:50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    transform: translateX(-50%) translateY(-50%);
    width: 200px;
    height: 200px;
    // border: 2px solid #cccccc;
    z-index: 999;
    .nav{
      position: relative;
      z-index: 999;
      color: #999999;
      font-size: 13px;
      .a{
        transition: all 0.5s;
        margin: 0 10px;
        text-decoration: underline;
        cursor: pointer;
      }
      .a:hover{
        transition: all 0.3s;
        color: #000000;
        // text-shadow: 0px 0px 13px black;
      }
    }
  }
}